.login-container {
  background-color: #fffffe;
  border: 1px solid #1f1f1f;
  border-radius: 8px;
  margin: 32px auto;
  max-width: 320px;
}

.login-container form {
  display: flex;
  flex-direction: column;
  margin: 32px auto;
  max-width: 240px;
}

.login-container h3 {
  color: #001e1d;
  margin: 8px 0;
  text-align: center;
}

.login-container input {
  border: 1px solid #1f1f1f;
  border-radius: 6px;
  font-size: 14px;
  margin: 8px 0;
  outline: none;
  padding: 4px 8px;
}

.login-container button {
  background-color: #f9bc60;
  border: 1px solid #f9bc60;
  border-radius: 4px;
  color: #001e1d;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  margin: 8px 0;
  padding: 4px;
  transition: background-color 0.2s;
  width: 100%;
}

.login-container button:hover {
  background-color: #f1ae4a;
}

.wallet-logo {
  width: 100px;
}

.title-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 16px;
  font-family: 'Georama', sans-serif;
}

.title-container  h3 {
  font-size: 24px
}